.App {
  width: 100%;
  height: 100vh;
  text-align: center;
  overflow-x: hidden;
}

.wrapper {
  margin: 0px 50px;
  display: flex;
  text-align: start;
}

.Section {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0px 50px;
}

.Box {
  width: 90%;
  height: 700px;
  border-radius: 10px;
  margin-right: 5px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px,
    rgba(0, 0, 0, 0.22) 0px 10px 10px;
  overflow: hidden;
  position: relative;
  background-color: white;
  margin-bottom: 50px;
}

.deck {
  position: absolute;
  top: 30%;
  left: 40%;
  width: 300px;
  height: 200px;
  will-change: transform;
  display: flex;
  align-items: center;
  justify-content: center;
  touch-action: none;
}

.deck > div {
  background-color: white;
  background-size: auto 85%;
  background-repeat: no-repeat;
  background-position: center center;
  width: 45vh;
  max-width: 150px;
  height: 85vh;
  max-height: 285px;
  will-change: transform;
  border-radius: 10px;
  box-shadow: 0 12.5px 100px -10px rgba(50, 50, 73, 0.4),
    0 10px 10px -10px rgba(50, 50, 73, 0.3);
}

.Box2 {
  width: 600px;
  height: 500px;
  border-radius: 10px;
  margin-right: 5px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px,
    rgba(0, 0, 0, 0.22) 0px 10px 10px;
  overflow: hidden;
  position: relative;
  perspective: 3000px;
  perspective-origin: 50% 50%;
  margin-bottom: 50px;
}

.Box0 {
  width: 50%;
  height: 200px;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px,
    rgba(0, 0, 0, 0.22) 0px 10px 10px;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 1);
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Bar {
  border: 2px solid white;
  width: 100%;
  margin: 0px 10px;
  overflow: hidden;
}
